    position: absolute;
    z-index: 10000;
    opacity: 0;
    pointer-events: none;
    top: 0;
    left: 0;
    width: 160px;
    height: 102px;
    transform: translateX(-50%);
    font-size: 10px;

    padding: .5rem;
    color: #212529;
    text-align: left;
    list-style: none;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0, 0, 0, .15);
    border-radius: .25rem;

    &.show {
        opacity: 1;
        pointer-events: all;
    }

    dl {
        display: flex;
        flex-wrap: wrap;
        margin: 0;

        dt, dd {
            display: block;
            margin-bottom: 0;
        }

        dt {
            flex-grow: 1;
            min-width: 50%;

            ~ dt {
                margin-top: .5rem;
            }
        }

        dd {
            flex-shrink: 1;

            ~ dd {
                margin-top: .5rem;
            }
        }

        dd:last-child {
            margin-bottom: 0;
        }
    }
}

svg.model {
    overflow: visible;
    user-select: none;
}

.node {
    rect, circle, ellipse, polygon {
        stroke: #333;
        fill: #fff;
        stroke-width: 1.5px;
    }

    &.state- {
        &active {
            rect, circle, ellipse, polygon {
                stroke: #007bff;
                stroke-width: 3px;

                + .label {
                    color: #007bff;
                    font-weight: bold;
                }
            }
        }

        &previous {
            rect, circle, ellipse, polygon {
                stroke: #007bff;
                fill: #6c757d;

                + .label {
                    color: #fff;
                }
            }
        }
    }

    .label > g > * {
        overflow: visible;
    }

    /*&:hover {*/
    /*    .tooltip {*/
    /*        opacity: 1;*/
    /*        pointer-events: all;*/
    /*    }*/
    /*}*/
}

.edgePath {
    path {
        stroke: #333;
        fill: #333;
        stroke-width: 1.5px;
    }

    &.active {
        path {
            stroke: #007bff;
            fill: #007bff;
            stroke-width: 2px;
        }
    }
}
